import React from "react";
import FormGroup from "@material-ui/core/FormGroup";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import Checkbox from "@material-ui/core/Checkbox";
import kamoku from "./kamoku";

export default function CheckboxLabels() {
  const [state, setState] = React.useState({});

  const handleChange = (event) => {
    setState({ ...state, [event.target.name]: event.target.checked });
  };

  return (
    <FormGroup row>
      <FormControlLabel
        control={<Checkbox onChange={handleChange} color="primary" />}
        label={kamoku[0]["15"]}
      />
      <FormControlLabel
        control={<Checkbox onChange={handleChange} color="primary" />}
        label={kamoku[0]["16"]}
      />
      <FormControlLabel
        control={<Checkbox onChange={handleChange} color="primary" />}
        label={kamoku[0]["17"]}
      />
      <FormControlLabel
        control={<Checkbox onChange={handleChange} color="primary" />}
        label={kamoku[0]["18"]}
      />
      <FormControlLabel
        control={<Checkbox onChange={handleChange} color="primary" />}
        label={kamoku[0]["19"]}
      />
      <FormControlLabel
        control={<Checkbox onChange={handleChange} color="primary" />}
        label={kamoku[0]["20"]}
      />
      <FormControlLabel
        control={<Checkbox onChange={handleChange} color="primary" />}
        label={kamoku[0]["21"]}
      />
      <FormControlLabel
        control={<Checkbox onChange={handleChange} color="primary" />}
        label={kamoku[0]["22"]}
      />
      <FormControlLabel
        control={<Checkbox onChange={handleChange} color="primary" />}
        label={kamoku[0]["23"]}
      />
      <FormControlLabel
        control={<Checkbox onChange={handleChange} color="primary" />}
        label={kamoku[0]["24"]}
      />
      <FormControlLabel
        control={<Checkbox onChange={handleChange} color="primary" />}
        label={kamoku[0]["25"]}
      />
      <FormControlLabel
        control={<Checkbox onChange={handleChange} color="primary" />}
        label={kamoku[0]["26"]}
      />
      <FormControlLabel
        control={<Checkbox onChange={handleChange} color="primary" />}
        label={kamoku[0]["27"]}
      />
      <FormControlLabel
        control={<Checkbox onChange={handleChange} color="primary" />}
        label={kamoku[0]["28"]}
      />
    </FormGroup>
  );
}
