import React from "react";
import FormGroup from "@material-ui/core/FormGroup";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import Checkbox from "@material-ui/core/Checkbox";
import kamoku from "./kamoku";

export default function CheckboxLabels() {
  const [state, setState] = React.useState({});

  const handleChange = (event) => {
    setState({ ...state, [event.target.name]: event.target.checked });
  };

  return (
    <FormGroup row>
      <FormControlLabel
        control={<Checkbox onChange={handleChange} color="primary" />}
        label={kamoku[0]["1"]}
      />
      <FormControlLabel
        control={<Checkbox onChange={handleChange} color="primary" />}
        label={kamoku[0]["2"]}
      />
      <FormControlLabel
        control={<Checkbox onChange={handleChange} color="primary" />}
        label={kamoku[0]["3"]}
      />
      <FormControlLabel
        control={<Checkbox onChange={handleChange} color="primary" />}
        label={kamoku[0]["4"]}
      />
      <FormControlLabel
        control={<Checkbox onChange={handleChange} color="primary" />}
        label={kamoku[0]["5"]}
      />
      <FormControlLabel
        control={<Checkbox onChange={handleChange} color="primary" />}
        label={kamoku[0]["6"]}
      />
      <FormControlLabel
        control={<Checkbox onChange={handleChange} color="primary" />}
        label={kamoku[0]["7"]}
      />
      <FormControlLabel
        control={<Checkbox onChange={handleChange} color="primary" />}
        label={kamoku[0]["8"]}
      />
      <FormControlLabel
        control={<Checkbox onChange={handleChange} color="primary" />}
        label={kamoku[0]["9"]}
      />
      <FormControlLabel
        control={<Checkbox onChange={handleChange} color="primary" />}
        label={kamoku[0]["10"]}
      />
      <FormControlLabel
        control={<Checkbox onChange={handleChange} color="primary" />}
        label={kamoku[0]["11"]}
      />
      <FormControlLabel
        control={<Checkbox onChange={handleChange} color="primary" />}
        label={kamoku[0]["12"]}
      />
      <FormControlLabel
        control={<Checkbox onChange={handleChange} color="primary" />}
        label={kamoku[0]["13"]}
      />
      <FormControlLabel
        control={<Checkbox onChange={handleChange} color="primary" />}
        label={kamoku[0]["14"]}
      />
    </FormGroup>
  );
}
