import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import Divider from "@material-ui/core/Divider";
import bunrui from "./bunrui";
import CB1 from "./kyouka/checkbox1";
import CB2 from "./kyouka/checkbox2";
import CB3 from "./kyouka/checkbox3";
import CB4 from "./kyouka/checkbox4";
import CB5 from "./kyouka/checkbox5";
import CB6 from "./kyouka/checkbox6";
import CB7 from "./kyouka/checkbox7";
import CB8 from "./kyouka/checkbox8";
import CB9 from "./kyouka/checkbox9";
import SB1 from "./tyuuizikou/snackbar1";
import SB2 from "./tyuuizikou/snackbar2";
import SB3 from "./tyuuizikou/snackbar3";
import SB4 from "./tyuuizikou/snackbar4";
import SB5 from "./tyuuizikou/snackbar5";
import SB6 from "./tyuuizikou/snackbar6";
import SB7 from "./tyuuizikou/snackbar7";

const useStyles = makeStyles((theme) => ({
  root: {
    width: "100%",
    //    maxWidth: 360,
    backgroundColor: theme.palette.background.paper
  }
}));

export default function ListDividers() {
  const classes = useStyles();

  return (
    <List component="nav" className={classes.root} aria-label="mailbox folders">
      <Divider />
      <h1>{bunrui[0]["1"]}</h1>
      <h1>{bunrui[0]["2"]}</h1>
      <SB1 />
      <br />
      <Divider />
      <ListItem button divider>
        <CB1 />
      </ListItem>
      <h1>{bunrui[0]["3"]}</h1>
      <h1>{bunrui[0]["4"]}</h1>
      <SB2 />
      <br />
      <Divider />
      <ListItem button divider>
        <CB2 />
      </ListItem>
      <h1>{bunrui[0]["5"]}</h1>
      <SB3 />
      <br />
      <Divider />
      <ListItem button divider>
        <CB3 />
      </ListItem>
      <h1>{bunrui[0]["6"]}</h1>
      <SB4 />
      <br />
      <Divider />
      <ListItem button divider>
        <CB4 />
      </ListItem>
      <h1>{bunrui[0]["7"]}</h1>
      <Divider />
      <ListItem button divider>
        <CB5 />
      </ListItem>
      <h1>{bunrui[0]["8"]}</h1>
      <SB5 />
      <br />
      <Divider />
      <ListItem button divider>
        <CB6 />
      </ListItem>
      <h1>{bunrui[0]["9"]}</h1>
      <h1>{bunrui[0]["10"]}</h1>
      <Divider />
      <ListItem button divider>
        <CB7 />
      </ListItem>
      <h1>{bunrui[0]["11"]}</h1>
      <Divider />
      <ListItem button divider>
        <CB8 />
      </ListItem>
      <h1>{bunrui[0]["12"]}</h1>
      <SB6 />
      <br />
      <Divider />
      <ListItem button divider>
        <CB9 />
      </ListItem>
      <br />
      <SB7 />
    </List>
  );
}
