import React from "react";
import FormGroup from "@material-ui/core/FormGroup";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import Checkbox from "@material-ui/core/Checkbox";
import kamoku from "./kamoku";

export default function CheckboxLabels() {
  const [state, setState] = React.useState({});

  const handleChange = (event) => {
    setState({ ...state, [event.target.name]: event.target.checked });
  };

  return (
    <FormGroup row>
      <FormControlLabel
        control={<Checkbox onChange={handleChange} color="primary" />}
        label={kamoku[0]["42"]}
      />
      <FormControlLabel
        control={<Checkbox onChange={handleChange} color="primary" />}
        label={kamoku[0]["43"]}
      />
      <FormControlLabel
        control={<Checkbox onChange={handleChange} color="primary" />}
        label={kamoku[0]["44"]}
      />
      <FormControlLabel
        control={<Checkbox onChange={handleChange} color="primary" />}
        label={kamoku[0]["45"]}
      />
      <FormControlLabel
        control={<Checkbox onChange={handleChange} color="primary" />}
        label={kamoku[0]["46"]}
      />
      <FormControlLabel
        control={<Checkbox onChange={handleChange} color="primary" />}
        label={kamoku[0]["47"]}
      />
      <FormControlLabel
        control={<Checkbox onChange={handleChange} color="primary" />}
        label={kamoku[0]["48"]}
      />
      <FormControlLabel
        control={<Checkbox onChange={handleChange} color="primary" />}
        label={kamoku[0]["49"]}
      />
      <FormControlLabel
        control={<Checkbox onChange={handleChange} color="primary" />}
        label={kamoku[0]["50"]}
      />
      <FormControlLabel
        control={<Checkbox onChange={handleChange} color="primary" />}
        label={kamoku[0]["51"]}
      />
      <FormControlLabel
        control={<Checkbox onChange={handleChange} color="primary" />}
        label={kamoku[0]["52"]}
      />
      <FormControlLabel
        control={<Checkbox onChange={handleChange} color="primary" />}
        label={kamoku[0]["53"]}
      />
    </FormGroup>
  );
}
