import React from "react";
import FormGroup from "@material-ui/core/FormGroup";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import Checkbox from "@material-ui/core/Checkbox";
import kamoku from "./kamoku";

export default function CheckboxLabels() {
  const [state, setState] = React.useState({});

  const handleChange = (event) => {
    setState({ ...state, [event.target.name]: event.target.checked });
  };

  return (
    <FormGroup row>
      <FormControlLabel
        control={<Checkbox onChange={handleChange} color="primary" />}
        label={kamoku[0]["29"]}
      />
      <FormControlLabel
        control={<Checkbox onChange={handleChange} color="primary" />}
        label={kamoku[0]["30"]}
      />
      <FormControlLabel
        control={<Checkbox onChange={handleChange} color="primary" />}
        label={kamoku[0]["31"]}
      />
      <FormControlLabel
        control={<Checkbox onChange={handleChange} color="primary" />}
        label={kamoku[0]["32"]}
      />
      <FormControlLabel
        control={<Checkbox onChange={handleChange} color="primary" />}
        label={kamoku[0]["33"]}
      />
      <FormControlLabel
        control={<Checkbox onChange={handleChange} color="primary" />}
        label={kamoku[0]["34"]}
      />
      <FormControlLabel
        control={<Checkbox onChange={handleChange} color="primary" />}
        label={kamoku[0]["35"]}
      />
      <FormControlLabel
        control={<Checkbox onChange={handleChange} color="primary" />}
        label={kamoku[0]["36"]}
      />
      <FormControlLabel
        control={<Checkbox onChange={handleChange} color="primary" />}
        label={kamoku[0]["37"]}
      />
      <FormControlLabel
        control={<Checkbox onChange={handleChange} color="primary" />}
        label={kamoku[0]["38"]}
      />
      <FormControlLabel
        control={<Checkbox onChange={handleChange} color="primary" />}
        label={kamoku[0]["39"]}
      />
      <FormControlLabel
        control={<Checkbox onChange={handleChange} color="primary" />}
        label={kamoku[0]["40"]}
      />
      <FormControlLabel
        control={<Checkbox onChange={handleChange} color="primary" />}
        label={kamoku[0]["41"]}
      />
    </FormGroup>
  );
}
